.seats {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px; 
  max-width: fit-content;
  flex-wrap: wrap; 
  padding: 10px 0;
}

.seats-input {
  width: 70px;
  height: 40px; 
  border-radius: 25px; 
  margin-left: 10px;
  text-align: center;
  font-size: 18px; 
  font-weight: bold;
  border: 2px solid #ddd; 
  padding: 5px;
  background-color: #f8f8f8; 
  transition: background-color 0.3s ease, transform 0.2s ease; 
}

.seats-input:focus {
  outline: none;
  border-color: #4caf50;
  background-color: #e8f5e9; 
  transform: scale(1.05); 
}


.seats-input:hover {
  background-color: #e0e0e0; 
  border-color: #888; 
  transform: scale(1.02); 
}


@media (max-width: 768px) {
  .seats {
      flex-direction: column; 
      gap: 10px;
  }

  .seats-input {
      width: 60px; 
      height: 35px; 
      font-size: 16px; 
  }
}
