.last_booking_details_container {
  padding: 20px;
  border-radius: 8px;
  border: 2px solid #ddd; 
  background-color: #f7f9fc;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05); 
  transition: box-shadow 0.3s ease, transform 0.3s ease;
}


.last_booking_details_container:hover {
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1);
  transform: translateY(-2px);
}


.last_booking_details_container_main {
  display: flex;
  flex-direction: column;
  color: #333; 
}

.last_booking_details_header {
  font-size: 24px;
  margin-bottom: 12px;
  color: #222;
  font-weight: 600;
  border-bottom: 1px solid #ddd;
  padding-bottom: 8px;
}


.seats_container {
  margin-bottom: 15px;
}


.seats_header {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #444;
}


.seats_container .seats {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 0;
}

.seat_value {
  width: 60px;
  padding: 8px;
  border-radius: 8px;
  background-color: #00cfcf;
  color: #fff;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.seat_value:hover {
  background-color: #009999;
  transform: scale(1.05);
}

.slot, .movie {
  margin-bottom: 12px;
  font-size: 18px;
  font-weight: 600;
  color: #333;
}

.slot span, .movie span {
  font-weight: 400;
  font-size: 16px;
  color: #555;
}

.last_booking_details_container .no_previous_booking_msg {
  font-size: 16px;
  color: #777;
  text-align: center;
  margin-top: 10px;
}
