
.SS_heading {
  width: 100%;
  font-size: 24px; 
  color: #333; 
  font-weight: 600; 
  text-align: left; 
  margin-bottom: 20px; 
  padding-bottom: 5px; 
  border-bottom: 2px solid #ddd; 
  letter-spacing: 0.5px; 
}

.SS_main_container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px; 
  justify-content: flex-start;
  margin-bottom: 30px; 
  padding: 10px;
  color: #444; 
}

@media (max-width: 768px) {
  .SS_heading {
      font-size: 20px; 
      text-align: center; 
  }

  .SS_main_container {
      justify-content: center;
      gap: 10px; 
  }
}
