.modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh; 
  background-color: rgba(0, 0, 0, 0.45); 
  overflow: auto;
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(0); 
  transition: transform 0.3s ease, opacity 0.3s ease; 
}


.modal-container.active {
  transform: scale(1); 
  opacity: 1; 
}

.modal {
  background-color: #fff;
  margin: auto;
  border-radius: 10px; 
  width: 50%; 
  max-width: 500px; 
  height: auto; 
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); 
  overflow: hidden; 
}


.modal-header {
  font-size: 22px;
  font-weight: 600;
  letter-spacing: 0.5px;
  padding: 15px;
  width: 100%;
  background-color: #f5f5f5; 
  border-bottom: 2px solid #ddd;
  color: #333; 
}


.modal-body {
  padding: 20px 30px;
  font-size: 18px;
  color: #555; 
  text-align: justify;
  width: 100%;
  line-height: 1.5; 
}

.modal-footer {
  padding: 15px;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  background-color: #f5f5f5; 
  border-top: 2px solid #ddd; 
}


.modal-footer button {
  font-size: 18px;
  font-weight: bold;
  border-radius: 25px; 
  padding: 8px 15px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease; 
}


.modal-footer :first-child {
  background-color: #4caf50;
  color: #fff;
}

.modal-footer :first-child:hover {
  background-color: #45a049; 
  transform: scale(1.05); 
}


.modal-footer :last-child {
  background-color: rgba(255, 0, 0, 0.8); 
  color: #fff;
}

.modal-footer :last-child:hover {
  background-color: rgba(255, 0, 0, 1);
  transform: scale(1.05); 
}


@keyframes activemodal {
  from {
      opacity: 0;
      transform: scale(0.8); 
  }
  to {
      opacity: 1;
      transform: scale(1); 
  }
}

@media (max-width: 768px) {
  .modal {
      width: 80%; 
  }

  .modal-header {
      font-size: 18px; 
  }

  .modal-body {
      font-size: 16px; 
  }

  .modal-footer button {
      font-size: 16px; 
      padding: 5px 10px; 
  }
}
