
.Slot_container {
  text-align: center;
  padding: 30px 20px; 
}


.TS_heading {
  text-align: center;
  font-size: 30px;
  margin-bottom: 25px;
  color: #333; 
  font-weight: 700; 
  letter-spacing: 0.5px;
}


.TS_main_container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px; 
  justify-content: center;
  margin-top: 25px;
}


.time-slot-box {
  background-color: #292525; 
  padding: 15px 25px;
  border-radius: 10px;
  cursor: pointer;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #f1f1f1; 
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.2s ease;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  min-width: 140px;
  max-width: 200px;
}


.time-slot-box:hover {
  background-color: #7b1e1e; 
  transform: scale(1.05);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}


.time-slot-box.selected {
  background-color: #d4ff88; 
  color: #572020; 
  box-shadow: 0px 6px 12px rgba(0, 128, 0, 0.2);
  transform: scale(1.05); 
}

@media (max-width: 768px) {
  .TS_heading {
      font-size: 26px;
  }

  .time-slot-box {
      font-size: 16px;
      padding: 12px 18px;
  }

  .TS_main_container {
      justify-content: center;
      gap: 12px;
  }
}
