
.SM_main_container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px; 
  justify-content: center;
  margin-top: 30px; 
  padding: 10px;
  max-width: 1200px;
  margin: 20px auto;
}

.SM_heading {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  color: #333; 
  margin-bottom: 25px;
  letter-spacing: 0.5px; 
}

.movie-box {
  background-color: #292525;
  color: #f1f1f1;
  padding: 15px 25px;
  border-radius: 10px;
  cursor: pointer;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.2s ease;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.15);
  min-width: 120px;
  max-width: 200px;
}


.movie-box:hover {
  background-color: #7b1e1e;
  transform: scale(1.05);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}


.movie-box.selected {
  background-color: #d4ff88; 
  color: #572020;
  box-shadow: 0px 6px 12px rgba(0, 128, 0, 0.2);
  transform: scale(1.05);
}


@media (max-width: 768px) {
  .movie-box {
      font-size: 16px;
      padding: 10px 15px;
  }

  .SM_heading {
      font-size: 24px;
  }
}
