.container {
    width: 80%;
    max-width: 1200px;
    margin: 30px auto;
    padding: 30px;
    background-color: #f9f9f9; 
    border-radius: 8px;
    border: 1px solid #e0e0e0;
    color: #333;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1); 
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.container:hover {
    transform: translateY(-5px);
    box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.15);
}

.select_movie_component {
    flex-grow: 1;
}

.BN-btn {
    padding: 12px 24px;
    border: none;
    margin-top: 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
    color: #333;
    font-weight: 600;
    background-color: #e8e8e8;
    border: 2px solid #666;
    position: relative;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.3s ease;
}

.BN-btn:hover {
    background-color: #ff0055;
    color: #ffffff;
    transform: translateY(-2px);
    box-shadow: 0px 6px 15px rgba(255, 0, 55, 0.4);
}

.wrapper {
    display: flex;
    flex-grow: 0;
    overflow: visible;
    gap: 20px;
}

.BN-btn:active {
    transform: scale(0.98);
    box-shadow: none;
}

.BN-btn:focus {
    outline: none;
    box-shadow: 0px 0px 0px 3px rgba(255, 0, 85, 0.4);
}
